<template>
  <div class="zr">
    <div class="water">
      <div class="water-c">
        <div class="water-1"> </div>
        <div class="water-2"> </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'bolang'
}
</script>
<style lang="stylus" scoped>
.zr {
  color: white;
  background: #ec1f3f;
  width: 100%;
  height: 30px;
  position: relative;
}

.water {
  position: absolute;
  left: 0;
  bottom: -10px;
  height: 30px;
  width: 100%;
  z-index: 1;
}

.water-c {
  position: relative;
}

.water-1 {
  background: url("data:image/svg+xml;base64,") repeat-x;
  background-size: 600px;
  animation: wave-animation-1 4.5s infinite linear;
}

.water-2 {
  top: 5px;
  background: url("data:image/svg+xml;base64,") repeat-x;
  background-size: 600px;
  animation: wave-animation-2 8s infinite linear;
}

.water-1,
.water-2 {
  position: absolute;
  width: 100%;
  height: 30px;
}

.back-white {
  background: #fff;
}

@keyframes wave-animation-1 {
  0% {
    background-position: 0 top;
  }

  100% {
    background-position: 600px top;
  }
}

@keyframes wave-animation-2 {
  0% {
    background-position: 0 top;
  }

  100% {
    background-position: 600px top;
  }
}
</style>



